<template>
  <div class="map-box">
    <div id="viewDiv" ref="viewDiv"></div>
    <!-- 图例 -->
    <div id="legendbar">
      <span>图例</span>
      <Legend :data="legendData" id="legend"></Legend>
    </div>
    <!-- 筛选条件 -->
    <div id="toggleBox" :class="{activeBox: boxstate === 1}">
      <div ref="searchBox" id="searchBox">
        <h3>图层叠加</h3>
        <RadioGroup v-model="searchType" @on-change="radioSearchType">
          <Radio label="1" border>核查人员分布</Radio>
          <Radio label="2" border>核查任务分布</Radio>
          <Radio label="3" border>核查轨迹分布</Radio>
        </RadioGroup>
        <Form ref="searchForm1" :model="searchForm1" :label-width="100" v-if="searchType === '1'">
          <FormItem prop="ch005" label="人员状态：">
            <i-select v-model.trim="searchForm1.ch005" style="width: 100%" placeholder="请选择人员状态">
              <i-option value="-1">全部</i-option>
              <i-option value="0">离线</i-option>
              <i-option value="1">在线</i-option>
            </i-select>
          </FormItem>
          <FormItem prop="sa019" label="人员真实姓名：">
            <Input v-model.trim="searchForm1.sa019" placeholder="请输入人员真实姓名"/>
          </FormItem>
          <FormItem prop="sa012" label="人员手机号：" style="margin-bottom: 10px">
            <Input v-model.trim="searchForm1.sa012" placeholder="请输入人员手机号"/>
          </FormItem>
        </Form>
        <Form ref="searchForm2" :model="searchForm2" :label-width="85" v-else-if="searchType === '2'">
          <FormItem prop="cl010" label="任务年份：">
            <DatePicker :editable="false" type="year" v-model.trim="searchForm2.cl010" placement="right-end" placeholder="请选择任务年份" style="width: 100%"></DatePicker>
          </FormItem>
          <FormItem prop="cl002" label="任务名称：" style="margin-bottom: 10px">
            <Input v-model.trim="searchForm2.cl002" placeholder="请输入任务名称"/>
          </FormItem>
        </Form>
        <Form ref="searchForm3" :model="searchForm3" :label-width="85" v-else-if="searchType === '3'">
          <FormItem prop="taskName" label="任务名称：">
            <Input v-model.trim="searchForm3.taskName" placeholder="请输入任务名称"/>
          </FormItem>
          <FormItem prop="ledgerId" label="任务台账：" style="margin-bottom: 10px">
            <i-select filterable clearable v-model.trim="searchForm3.ledgerId" style="width: 100%" placeholder="请选择任务台账">
              <i-option v-for="item in ledgerList" :value="item.value">{{ item.label }}</i-option>
            </i-select>
          </FormItem>
        </Form>
        <img src="../../../assets/dynamicManagement/toggle.png" alt="" id="toggleImg" @click="boxstate = boxstate === 1 ? 0 :1">
        <div class="searchBtns">
          <Button type="primary" @click="addLayers">图层叠加</Button>
          <Button @click="cancelAddLayers">取消</Button>
        </div>
      </div>
    </div>
    <!-- 图层控制面板 -->
    <div id="controlLayerBox">
      <div ref="layerBox" id="layerBox">
        <h3>图层控制</h3>
        <p>
          <Checkbox label="1" @on-change="handlePatroluser" v-model.trim="patroluser">核查人员分布</Checkbox>
          <Checkbox label="2" @on-change="handlePatroltask" v-model.trim="patroltask">核查任务分布</Checkbox>
          <Checkbox label="3" @on-change="handlePatroltrack" v-model.trim="patroltrack">核查轨迹分布</Checkbox>
        </p>
      </div>
    </div>
    <!-- 根据筛选条件显示表格 -->
<!--    <div class="sta_box" :class="{activeTable: tablestate === 2}">-->
<!--      <div class="con_box">-->
<!--        <Button :icon="tablestate === 2 ? 'md-contract' : 'md-expand'" @click="handleTaleBox" size="small" class="boxBtn"></Button>-->
<!--        <div class="bigBox" :class="{smallToBig:tablestate === 2}">-->
<!--          <div class="smallBox" v-if="searchType === '1'">-->
<!--            <Table border stripe :columns="columns1" :data="data1" highlight-row :max-height="tableHeight">-->
<!--            </Table>-->
<!--          </div>-->
<!--          <div class="smallBox" v-if="searchType === '2'">-->
<!--            <Table border stripe :columns="columns2" :data="data2" highlight-row :max-height="tableHeight">-->
<!--            </Table>-->
<!--          </div>-->
<!--          <div class="smallBox" v-if="searchType === '3'">-->
<!--            <Table border stripe :columns="columns3" :data="data3" highlight-row :max-height="tableHeight">-->
<!--            </Table>-->
<!--          </div>-->
<!--        </div>-->
<!--      </div>-->
<!--    </div>-->
  </div>
</template>

<script>
import arcgisPackage from '../../../utils/arcgisTools'
import config from '../../../setting/mapConfig'
import { tdtlayer, tdtyNoteslayer, tdtclayer, tdtcNoteslayer, redLine, shengjie } from '@/utils/layers'
import { cacuDMS, renderYearTime } from '@/utils/common'
import Basemapthumbnail from '../../../assets/map_images/Basemapthumbnail.png'
import Basemapthumbnail1 from '../../../assets/map_images/Basemapthumbnail1.png'
import Legend from '@/components/Legend'
// 离线图标
import patrolUserGray from '../../../assets/boundaryMarker_images/guai_point3.png'
// 在线图标
// presetBm3---核查任务分布
import patrolUserRed from '../../../assets/boundaryMarker_images/preset_bm3.png'
// 巡查中图标
import patrolUserBlue from '../../../assets/boundaryMarker_images/preset_sign3.png'
import {
  // 台账列表
  getLedgerList
} from '@/api/taskManager'
import {
  // 实时位置
  listPhoneUserToView,
  // 核查任务分布
  listCdByTask,
  // 轨迹分布
  trackDistribution
} from '@/api/workRegulation'
export default {
  name: 'workRegulation',
  data () {
    return {
      legendData: [
        {
          type: 1,
          name: '离线',
          background: 'url(' + patrolUserGray + ')',
          id: 'patrolUserGray'
        },
        {
          type: 1,
          name: '在线',
          background: 'url(' + patrolUserRed + ')',
          id: 'patrolUserRed'
        }
        // {
        //   type: 1,
        //   name: '巡查中',
        //   background: 'url(' + patrolUserBlue + ')',
        //   id: 'patrolUserBlue'
        // }
      ],
      popupTemplate: {}, // 点击地图中点的弹框对象
      loadComplete: false, // 地图加载完成状态
      /* 查询条件框展开收缩的标识 */
      boxstate: 0,
      searchType: '1',
      // 图层控制-核查人员分布
      patroluser: true,
      patroltask: false,
      patroltrack: false,
      // 查询条件-核查人员分布
      searchForm1: {
        ch005: '-1',
        sa019: '',
        sa012: ''
      },
      // 查询条件-核查任务分布
      searchForm2: {
        cl010: '',
        cl002: ''
      },
      // 查询条件-核查轨迹分布
      searchForm3: {
        ledgerId: '',
        taskName: ''
      },
      // 任务台账列表
      ledgerList: [],
      // 放大缩小表格状态
      tablestate: 0,
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 326) / 2),
      /* 核查人员---表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '用户名',
          align: 'center',
          key: 'SA008',
          ellipsis: true,
          tooltip: true,
          minWidth: 200
        },
        {
          title: '手机号码',
          align: 'center',
          key: 'SA012',
          ellipsis: true,
          tooltip: true,
          minWidth: 200
        },
        {
          title: '真实姓名',
          align: 'center',
          key: 'SA019',
          ellipsis: true,
          tooltip: true,
          minWidth: 200
        },
        {
          title: '所属部门',
          align: 'center',
          key: 'SA015',
          ellipsis: true,
          tooltip: true,
          minWidth: 200
        }
      ],
      data1: [],
      /* 核查任务---表格数据 */
      columns2: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '任务名称',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl002'
        },
        {
          title: '任务类型',
          align: 'center',
          key: 'cl004',
          width: 100,
          render: (h, params) => {
            let data = params.row.cl004
            if (data === 0) {
              return h('span', {}, '巡护')
            } else if (data === 1) {
              return h('span', {}, '核查')
            } else if (data === null) {
              return h('span', {}, '')
            }
          }
        },
        {
          title: '任务年份',
          align: 'center',
          width: 100,
          key: 'cl010'
        },
        {
          title: '任务级别',
          align: 'center',
          key: 'cl011',
          width: 100,
          render: (h, params) => {
            let data = params.row.cl011
            if (data === 1) {
              return h('span', {}, '国家级')
            } else if (data === 2) {
              return h('span', {}, '市级')
            } else if (data === null) {
              return h('span', {}, '')
            }
          }
        },
        {
          title: '任务台账',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          render: (h, params) => {
            let row = params.row
            let optionArr = []
            row.rlhdGroupList.forEach(item => {
              optionArr.push(item.name)
            })
            return h('div', {}, optionArr.join('，'))
          }
        },
        {
          title: '任务描述',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'cl009'
        }
      ],
      data2: [],
      /* 核查轨迹---表格数据 */
      columns3: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + 1)
          }
        },
        {
          title: '巡护记录名称',
          align: 'center',
          key: 'cy002',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
        },
        {
          title: '巡护开始时间',
          align: 'center',
          key: 'cy004',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
        },
        {
          title: '巡护结束时间',
          align: 'center',
          key: 'cy004',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
        },
        {
          title: '巡护次数',
          align: 'center',
          key: 'rwcs',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
        },
        {
          title: '巡护里程(m)',
          align: 'center',
          key: 'gjlc',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
        },
        {
          title: '巡护时长(小时)',
          align: 'center',
          key: 'rwsc',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
        }
      ],
      data3: [],
      // 核查任务图层
      addtaskLayer: {},
      // 存放轨迹数据
      pointArrays: []
    }
  },
  components: {
    Legend
  },
  mounted () {
    // 加载地图
    this.createMap()
    // 获取任务台账
    this.getLedgers()
    // 每个10秒调用 巡查人员，查看实时位置
    setInterval(this.getLedgerList, 10000)
  },
  methods: {
    // 获取实时位置数据
    async getLedgerList () {
      if (this.searchForm1.ch005 === '-1') {
        this.searchForm1.ch005 = ''
      }
      await listPhoneUserToView({
        data: {
          ch005: this.searchForm1.ch005,
          sa019: this.searchForm1.sa019,
          sa012: this.searchForm1.sa012
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.data
          // console.log(this.data1)
          // 添加巡查人员
          this.addPointToMap(res.data.data.data, config.inflectionPointcolor, 'graphicsLayer_patroluser')
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    // 获取核查任务分布
    async getTaskList () {
      await listCdByTask({
        data: {
          cl010: renderYearTime(this.searchForm2.cl010),
          cl002: this.searchForm2.cl002
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data2 = res.data.data.data
          this.layerAddData(this.data2)
          // 添加核查任务分布
          // this.addTaskToMap(res.data.data.data, config.inflectionPointcolor, 'graphicsLayer_patroltask')
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    // 获取轨迹分布
    async getTrackDistribution () {
      await trackDistribution({
        data: {
          ledgerId: this.searchForm3.ledgerId,
          taskName: this.searchForm3.taskName
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data3 = res.data.data
          // 添加核查轨迹
          this.pointArrays = res.data.data
          this.addTrackToMap(res.data.data)
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /**
     * @Description:初始化地图的函数
     * @author 张淼
     * @date 2019/5/5
     */
    async createMap () {
      // 加载arcgis地图的依赖
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let BasemapToggle = await arcgisPackage.BasemapToggle
      let GraphicsLayer = await arcgisPackage.GraphicsLayer
      // 天地图影像地图
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 天地图矢量地图
      let tdtylayer1 = await tdtclayer().then(res => { return res })
      // 天地图的矢量标注图层
      let tdtylayer2 = await tdtcNoteslayer().then(res => { return res })
      // 天地图影像标注图层
      let tdtylayer3 = await tdtyNoteslayer().then(res => { return res })
      // 省界
      let layerProvince = await shengjie().then(res => { return res })
      let customBasemap = new Basemap({
        baseLayers: [tdtylayer1, layerProvince, tdtylayer2],
        title: '矢量地图',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })
      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, layerProvince, tdtylayer3],
        title: '影像地图',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })
      this.map = new Map({
        basemap: customBasemap1
      })
      this.view = new MapView({
        map: this.map,
        container: 'viewDiv',
        center: config.centerPoint,
        zoom: 7
      })
      this.view.when(wes => {
        this.loadComplete = true
      })
      // 弹框内容
      this.popupTemplate = {
        title: '巡查人员详情',
        content: [{
          type: 'fields',
          fieldInfos: [{
            fieldName: '用户名'
          },
          {
            fieldName: '手机号'
          },
          {
            fieldName: '真实姓名'
          },
          {
            fieldName: '经度'
          },
          {
            fieldName: '纬度'
          },
          {
            fieldName: '邮箱'
          }
          ]
        }]
      }
      // 增加底图切换插件
      let basemapToggle = new BasemapToggle({
        view: this.view,
        nextBasemap: customBasemap
      })
      this.view.ui.add(basemapToggle, 'top-right')
      // 添加图例
      this.view.ui.add(document.getElementById('legendbar'), 'bottom-right')
      // 添加核查任务图层
      this.getLedgerList()
      //
      // // 获取实时位置数据
      // if (this.patroluser === true) {
      //   this.getLedgerList()
      // } else if (this.patroluser === false) {
      //   // 取消全部巡查人员
      //   this.removePatrolUserFromMapNoPicAll()
      // }
      // // 获取核查任务分布
      // if (this.patroltask === true) {
      //   this.getTaskList()
      // } else if (this.patroltask === false) {
      //   // 取消全部核查任务分布
      //   this.removePatrolTaskFromMapNoPicAll()
      // }
      // // 获取轨迹分布
      // if (this.patroltrack === true) {
      //   this.getTrackDistribution()
      // } else if (this.patroltrack === false) {
      //   // 取消全部核查任务分布
      //   this.removePatrolTrackFromMapNoPicAll()
      // }
    },
    /**
     * 添加添加巡查人员
     * @param pointArray 点数组
     * @param symbolColor 符号颜色
     * @param layerId 自定义图层id
     */
    async addPointToMap (pointArray, symbolColor, layerId) {
      let GraphicsLayer = await arcgisPackage.GraphicsLayer
      let Graphic = await arcgisPackage.Graphic
      let layer = this.map.findLayerById(layerId)
      let point = {}
      let pointGraphic
      let textGraphic
      let wayPoint // 不同巡查状态图标
      let status // 巡查状态
      let lon // 经度
      let lat // 纬度
      let features = []
      if (layer) {
        this.map.remove(layer)
      }
      let graphicsLayer = new GraphicsLayer({
        id: layerId,
        elevationInfo: 'on-the-ground',
        title: '实时位置'
      })
      if (pointArray && pointArray.length > 0) {
        for (let i = 0; i < pointArray.length; i++) {
          if (pointArray[i].CH003 === null) {
            continue
          } else if (pointArray[i].CH003.indexOf('°') !== -1) { // 度分秒转为经纬度
            lon = cacuDMS(pointArray[i].CH003)
            lat = cacuDMS(pointArray[i].CH004)
          } else if (pointArray[i].CH003.indexOf('.') !== -1) {
            lon = pointArray[i].CH003
            lat = pointArray[i].CH004
          }
          point = {
            type: 'point', // autocasts as new Point()
            longitude: lon,
            latitude: lat
          }
          if (pointArray[i].CH005 === 0) { // 离线
            wayPoint = patrolUserGray
            status = '离线'
          } else if (pointArray[i].CH005 === 1) { // 在线
            wayPoint = patrolUserRed
            status = '在线'
          }
          // else if (pointArray[i].CH005 === 2) { // 巡查中
          //   wayPoint = patrolUserBlue
          //   status = '巡查中'
          // }
          // 属性信息
          let lineAtt = {
            'id': pointArray[i].SA001,
            '用户名': pointArray[i].SA008,
            '手机号': '<a style="color: blue">' + pointArray[i].SA012 + '</a>',
            '真实姓名': pointArray[i].SA019,
            '经度': lon,
            '纬度': lat,
            '邮箱': pointArray[i].SA013,
            '状态': status
          }
          if (pointArray[i].CH005 === 0) {
            lineAtt['手机号'] = '<a style="color: blue">' + pointArray[i].SA012 + '</a>'
          }
          let markerSymbol = {
            type: 'picture-marker', // autocasts as new SimpleMarkerSymbol()
            url: wayPoint,
            width: 20,
            height: 20,
            outline: { // autocasts as new SimpleLineSymbol()
              color: [255, 255, 255],
              width: 1
            }
          }
          let textSymbol = {
            type: 'text', // autocasts as new TextSymbol()
            color: 'white',
            haloColor: 'black',
            haloSize: '1px',
            text: pointArray[i].SA019,
            xoffset: 0,
            yoffset: -20,
            font: { // autocast as new Font()
              size: 12,
              family: 'sans-serif',
              weight: 'bold'
            }
          }
          // 构造点对象
          pointGraphic = new Graphic({
            geometry: point,
            symbol: markerSymbol,
            attributes: lineAtt,
            popupTemplate: this.popupTemplate
          })
          textGraphic = new Graphic({
            geometry: point,
            symbol: textSymbol
          })
          features.push(pointGraphic)
          features.push(textGraphic)
          graphicsLayer.add(pointGraphic)
          graphicsLayer.add(textGraphic)
        }
      }
      // 如果问题点对象图层存在，则将新点位添加到该对象
      this.map.add(graphicsLayer)
    },
    /**
     * 核查轨迹分布
     * @param pointArray 点数组
     * @param symbolColor 符号颜色
     * @param layerId 自定义图层id
     */
    async addTrackToMap (pointArray) {
      let GraphicsLayer = await arcgisPackage.GeoJSONLayer
      let geoJsonLayerAry = new Array()
      pointArray.forEach(element => {
        if (element.st4ScsCg.length > 0) {
          element.st4ScsCg.forEach(i => {
            if (i.cg001 !== null) {
              let fLayerFlag = this.map.findLayerById(i.cg001 + 'f')
              if (fLayerFlag) {
                this.map.remove(fLayerFlag)
              }
            }
            // 轨迹样式、颜色等
            let renderer = {
              type: 'simple',
              symbol: {
                type: 'simple-line',
                color: 'blue',
                width: 2
              }
            }
            // 属性框
            let template = {
              title: '航迹数据详情',
              content: '巡查记录名称：{name}'
            }
            // debugger
            let geoJsonLayer
            geoJsonLayer = new GraphicsLayer({
              url: config.GeoJsonUrl + i.cg003,
              id: i.cg001 + 'f',
              renderer: renderer
              // popupTemplate: template
            })
            this.map.addMany([geoJsonLayer])
            geoJsonLayerAry.push(geoJsonLayer)
          })
        }
      })
      this.map.addMany(geoJsonLayerAry)
      // 图层添加成功后定位到该图层视图范围内
      geoJsonLayerAry[0].on('layerview-create', (event) => {
        this.view.extent = geoJsonLayerAry[0].fullExtent.expand(1.4) // 定位图层
      })
    },
    /**
     * 核查任务分布
     * @param pointArray 点数组
     * @param symbolColor 符号颜色
     * @param layerId 自定义图层id
     */
    // /* 将获取到的数据添加到图层中 */
    async layerAddData (data) {
      let GraphicsLayer = await arcgisPackage.GraphicsLayer
      let addtaskLayer
      if (this.map.findLayerById('taskLayer')) {
        this.map.findLayerById('taskLayer').removeAll()
        this.map.findLayerById('taskLayer').visible = true
        addtaskLayer = this.map.findLayerById('taskLayer')
      } else {
        addtaskLayer = new GraphicsLayer({
          id: 'taskLayer'
        })
      }
      // 清除原来图层的元素
      let Graphic = await arcgisPackage.Graphic
      // 用于存放斑块的数组
      let graphicArr1 = []
      // 封装面积增加的graphic数组
      data.forEach(val => {
        if (val.geometry) {
          // 绘制多边形
          let polygon = {
            type: 'polygon', // autocasts as Polygon
            rings: JSON.parse(val.geometry),
            spatialReference: 4326
          }
          let graphic = new Graphic({
            geometry: polygon,
            symbol: {
              type: 'simple-fill', // autocasts as SimpleFillSymbol
              color: [51, 133, 255, 0.5],
              style: 'solid',
              outline: {
                color: [51, 133, 255, 0.8],
                width: 1
              }
            }
          })
          addtaskLayer.add(graphic)
        }
      })
      this.map.add(addtaskLayer)
    },
    // 获取台账列表
    async getLedgers () {
      await getLedgerList({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          res.data.data.data.forEach((item, index) => {
            this.ledgerList.push({ 'value': item.id, 'label': item.name })
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    // 根据查询条件查询
    addLayers () {
      if (this.searchType === '1') { // 根据核查人员分布查询列表
        this.getLedgerList()
      } else if (this.searchType === '2') { // 根据核查任务分布查询列表
        this.getTaskList()
      } else if (this.searchType === '3') { // 根据核查轨迹查询列表
        this.getTrackDistribution()
      }
    },
    // 取消根据查询条件查询
    cancelAddLayers () {},
    // 更改核查人员取消选中状态
    handlePatroluser (val) {
      // 获取实时位置数据
      if (val === true) {
        this.getLedgerList()
      } else if (val === false) {
        // 取消全部巡查人员
        this.removePatrolUserFromMapNoPicAll()
      }
    },
    // 更改核查任务取消选中状态
    handlePatroltask (val) {
      // 获取核查任务分布
      if (val === true) {
        this.getTaskList()
      } else if (val === false) {
        // 取消全部核查任务分布
        this.removePatrolTaskFromMapNoPicAll()
      }
    },
    // 更改轨迹分布取消选中状态
    handlePatroltrack (val) {
      // 获取轨迹分布
      if (val === true) {
        this.getTrackDistribution()
      } else if (val === false) {
        // 取消全部核查任务分布
        this.removePatrolTrackFromMapNoPicAll()
      }
    },
    // 取消全部巡查人员
    removePatrolUserFromMapNoPicAll () {
      // 先判断这个问题点的图层有没有
      if (this.map.findLayerById('graphicsLayer_patroluser')) {
        this.map.findLayerById('graphicsLayer_patroluser').visible = false
      }
    },
    // 取消全部核查任务分布
    removePatrolTaskFromMapNoPicAll () {
      // 先判断这个问题点的图层有没有
      if (this.map.findLayerById('taskLayer')) {
        this.map.findLayerById('taskLayer').visible = false
      }
    },
    // 取消全部轨迹分布
    removePatrolTrackFromMapNoPicAll () {
      let pointArray = this.pointArrays
      if (pointArray) {
        pointArray.forEach(element => {
          if (element.st4ScsCg.length > 0) {
            element.st4ScsCg.forEach(i => {
              if (i.cg003 !== null) {
                this.map.findLayerById(i.cg001 + 'f').visible = false
              }
            })
          }
        })
      }
    },
    // 改变查询类型清空前一个类型所填写的数据
    radioSearchType (val) {
      if (val === '1') {
        // 查询条件-核查任务分布
        this.searchForm2 = {
          cl010: '',
          cl002: ''
        }
        // 查询条件-核查轨迹分布
        this.searchForm3 = {
          ledgerId: '',
          taskName: ''
        }
      } else if (val === '2') {
        // 查询条件-核查人员
        this.searchForm1 = {
          ch005: '-1',
          sa019: '',
          sa012: ''
        }
        // 查询条件-核查轨迹分布
        this.searchForm3 = {
          ledgerId: '',
          taskName: ''
        }
      } else if (val === '3') {
        // 查询条件-核查人员
        this.searchForm1 = {
          ch005: '-1',
          sa019: '',
          sa012: ''
        }
        // 查询条件-核查任务分布
        this.searchForm2 = {
          cl010: '',
          cl002: ''
        }
      }
    },
    // 放大缩小表格
    handleTaleBox () {
      if (this.tablestate === 2) {
        this.tablestate = 0
        this.tableHeight = Math.floor((window.innerHeight - 326) / 2)
        this.pageSize = Math.floor((Math.floor((Math.floor((window.innerHeight - 326) / 2) - 40) / 48) * 48 + 40) / 48)
      } else {
        // 放大
        this.tablestate = 2
        this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
        this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      }
    }
  }
}
</script>

